:root {
  --blue-color: #00acdc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  margin: 0;
  display: flex;
}

.container {
  justify-content: space-between;
  align-items: center;
  width: 1024px;
  padding: 0 50px;
  display: flex;
}

@media (width >= 1200px) {
  .container {
    width: 1200px;
  }
}

.navbar {
  align-items: center;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.navbar__list {
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navbar__list li:not(:last-child) {
  margin-right: 20px;
}

.items__list {
  align-items: center;
  height: 15px;
  list-style-type: none;
  display: flex;
}

.navbar__button {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.navbar__button:hover, .navbar__button.is-active {
  color: var(--blue-color);
}

.navbar__button.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
